<template>
  <div class="main">
    <input
      type="text"
      class="todo-input"
      placeholder="What needs to be done today, Adonis?"
      v-model="newTodo"
      @keyup.enter="addTodo"
    />
    <div class="buttons">
      <div>
        <button :class="{ active: filter == 'all' }" @click="filter = 'all'">
          All
        </button>
        <button
          :class="{ active: filter == 'active' }"
          @click="filter = 'active'"
        >
          Active
        </button>
        <button
          :class="{ active: filter == 'completed' }"
          @click="filter = 'completed'"
        >
          Completed
        </button>
        <transition name="fade">
          <button v-if="showClearCompletedButton" @click="clearCompleted">
            Clear Completed
          </button>
        </transition>
      </div>
    </div>
    <div class="extra-container">
      <div>
        <label
          ><div class="center">
            <input
              type="checkbox"
              :checked="!anyRemaining"
              @change="checkAllTodos"
              id="checky2"
            />Check All
          </div></label
        >
      </div>
      <div class="right">{{ remaining }} items left</div>
    </div>
    <transition-group enter-active-class="animated fadeInUp">
      <div
        v-for="(todo, index) in todosFiltered"
        :key="todo.id"
        class="todo-item"
      >
        <div class="todo-item-left">
          <input type="checkbox" v-model="todo.completed" id="checky" />
          <div
            v-if="!todo.editing"
            @dblclick="editTodo(todo)"
            class="todo-item-label"
            :class="{ completed: todo.completed }"
          >
            {{ todo.title }}
          </div>
          <input
            v-else
            class="todo-item-edit"
            type="text"
            v-model="todo.title"
            @blur="doneEdit(todo)"
            @keyup.enter="doneEdit(todo)"
            @keyup.esc="cancelEdit(todo)"
            v-focus
          />
        </div>
        <div class="remove-item" @click="removeTodo(index)">&times;</div>
      </div>
    </transition-group>
    <div class="creds">Made with 🤍 by Luno#0002</div>
  </div>
</template>

<script>
export default {
  name: "Todo",
  data() {
    return {
      newTodo: "",
      idForTodo: 0,
      beforeEditCache: "",
      filter: "all",
      todos: [],
    };
  },
  mounted() {
    if (localStorage.todos) {
      this.todos = JSON.parse(localStorage.todos);
    }
  },
  computed: {
    remaining() {
      return this.todos.filter((todo) => !todo.completed).length;
    },
    anyRemaining() {
      return this.remaining != 0;
    },
    todosFiltered() {
      if (this.filter == "all") {
        return this.todos;
      } else if (this.filter == "active") {
        return this.todos.filter((todo) => !todo.completed);
      } else if (this.filter == "completed") {
        return this.todos.filter((todo) => todo.completed);
      }
      return this.todos;
    },
    showClearCompletedButton() {
      return this.todos.filter((todo) => todo.completed).length > 0;
    },
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
  watch: {
    todos: {
      handler(newTodo) {
        localStorage.todos = JSON.stringify(newTodo);
      },
      deep: true,
    },
  },
  methods: {
    addTodo() {
      if (this.newTodo.trim().length == 0) {
        return;
      }
      this.todos.push({
        id: this.idForTodo,
        title: this.newTodo,
        completed: false,
        editing: false,
      });
      this.newTodo = "";
      this.idForTodo++;
    },
    editTodo(todo) {
      this.beforeEditCache = todo.title;
      todo.editing = true;
    },
    doneEdit(todo) {
      if (todo.title.trim() == "") {
        todo.title = this.beforeEditCache;
      }
      todo.editing = false;
    },
    cancelEdit(todo) {
      todo.title = this.beforeEditCache;
      todo.editing = false;
    },
    removeTodo(index) {
      this.todos.splice(index, 1);
    },
    checkAllTodos() {
      this.todos.forEach((todo) => (todo.completed = event.target.checked));
    },
    clearCompleted() {
      this.todos = this.todos.filter((todo) => !todo.completed);
    },
  },
};
</script>

<style lang="scss">
.todo-input {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 40px;
  border-radius: 3px;
  border: solid 1px rgb(47, 51, 54);

  &:focus {
    outline: 0px;
  }

  &::selection {
    background: rgba(255, 255, 255, 0.3);
  }
}
.todo-item {
  @media only screen and (max-width: 600px) {
    margin-top: 9px;
  }
  border: solid 1px rgb(47, 51, 54);
  border-radius: 3px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.1);
}

.todo-item-label {
  @media only screen and (max-width: 600px) {
    font-size: 26px;
  }
  font-size: 20px;
  padding: 10px;
  max-width: 600px;
  width: 100%;

  &:hover {
    cursor: pointer;
  }
}

.remove-item {
  @media only screen and (max-width: 600px) {
    font-size: 48px;
  }
  font-size: 38px;
  margin-right: 12px;
  cursor: pointer;
  margin-left: 14px;
  &:hover {
    color: red;
    transition: 0.2s;
  }
}

.todo-item-left {
  display: flex;
  align-items: center;
  width: 100%;
}

.todo-item-edit {
  background: rgba(255, 255, 255, 0.05);
  font-size: 20px;
  color: rgba(255, 255, 255, 0.8);
  margin-left: 12px;
  width: 100%;
  padding: 10px;
  border: solid 1px rgba(255, 255, 255, 0.192);
  border-radius: 3px;
  font-display: "Avenir", Helvetica, Arial, sans-serif;

  &:focus {
    outline: none;
  }
}

.completed {
  text-decoration: line-through;
  color: grey;
}

#checky {
  margin-left: 12px;
  height: 24px;
  width: 24px;
}

#checky2 {
  margin-left: 12px;
  height: 24px;
  width: 24px;
  margin-right: 12px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.extra-container {
  background: rgba(255, 255, 255, 0.01);
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  margin-bottom: 14px;
  padding: 12px 0px;
  border-radius: 3px;
}

.right {
  margin-right: 12px;
}

.active {
  background: rgba(255, 255, 255, 0.6);
}

button {
  @media only screen and (max-width: 600px) {
    font-size: 14px;
  }
  background: rgba(255, 255, 255, 0.1);
  border: solid 1px rgba(255, 255, 255, 0.192);
  color: white;
  padding: 10px 10px;
  margin-top: 4px;
  font-weight: bold;
  border-radius: 3px;
  font-size: 16px;
}

.buttons {
  @media only screen and (max-width: 600px) {
    justify-content: center;
  }
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 8px;
  button:hover {
    background: rgba(255, 255, 255, 0.6);
  }
}
// CSS Transitions
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.creds {
  font-size: 14px;
  align-items: center;
  display: flex;
  font-weight: bolder;
  justify-content: center;
  width: 100%;
}
</style>



