<template>
  <div id="app" class="container">
    <img src="./assets/logo.png" class="logo" />
    <Todo></Todo>
  </div>
</template>

<script>
import Todo from "./components/Todo";

export default {
  name: "App",
  components: {
    Todo,
  },
};
</script>

<style>
@import "./assets/styles/animate.css";
* {
  box-sizing: border-box;

}

::selection {
  background: rgba(255, 255, 255, 0.3);
}
body {
  background: #121212;
}

.container {
  max-width: 600px;
  margin: 0 auto;
}

input {
  max-width: 600px;
}

.logo {
  display: block;
  margin: 20px auto;
  height: 75px;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-size: 20px;
}

input[type="checkbox"] {
  filter: invert(100%) saturate(0) brightness(1.7) opacity(20%);
}
</style>



